body {
  font-family: "Pontano Sans";
  min-width: 400px;
}

.navbar {
  z-index: 3;
}
.navbar-collapse {
  margin: 10px 20px;
}

.nav-item {
  font-size: 16px;
}

.links {
  transition: color 0.3s;
}

.links:hover {
  color: goldenrod;
}

.main {
  width: 100%;
  height: 100vh;
  background: url('../assets/world-image.jpg') center center;
  background-size: cover;
}

.main-content {
  width: 100%;
  height: 100%;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.7);
  
}

.title {
  color: white;
  text-align: center;
  font-size: 2.5rem;
  margin-top: 20px;
}

.col {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  min-width: 400px;
}

label {
  font-size: 20px;
}

.col div {
  width: 250px;
}

input {
  position: relative;
  width: 60%;
  display: block;
  height: 50px;
  border-color: transparent;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 5px;
  text-align: center;
}